<template>
  <div class="scenario-modify">
    <div v-if="loading" class="text-center my-5">
      <div class="spinner-border text-primary" role="status">
        <span class="visually-hidden"> ...</span>
      </div>
      <p class="mt-3">  ...</p>
    </div>
    
    <div v-else-if="error" class="alert alert-danger my-5">
      {{ error }}
    </div>
    
    <div v-else-if="!currentScenario" class="alert alert-warning my-5">
         .
    </div>
    
    <div v-else>
      <div class="d-flex justify-content-between align-items-center mb-4">
        <h1> </h1>
        <router-link :to="{ name: 'ScenarioDetail', params: { id } }" class="btn btn-outline-secondary">
          
        </router-link>
      </div>
      
      <div class="card mb-4">
        <div class="card-header bg-primary text-white">
          <h2 class="h5 mb-0">{{ currentScenario.title }}</h2>
        </div>
        <div class="card-body">
          <div class="mb-3">
            <h3 class="h6"> </h3>
            <p>{{ currentScenario.main_disease }}</p>
          </div>
          
          <div class="mb-3">
            <h3 class="h6">  </h3>
            <ul class="list-group list-group-flush">
              <li class="list-group-item" v-for="(value, key) in currentScenario.personal_info" :key="key">
                <strong>{{ formatKey(key) }}:</strong> {{ formatValue(value) }}
              </li>
            </ul>
          </div>
          
          <div v-if="currentScenario.additional_info">
            <h3 class="h6"> </h3>
            <p>{{ currentScenario.additional_info }}</p>
          </div>
        </div>
      </div>
      
      <div class="card mb-4">
        <div class="card-header bg-light">
          <h3 class="h5 mb-0">  </h3>
        </div>
        <div class="card-body">
          <div class="scenario-content">
            <pre class="scenario-text">{{ currentScenario.content }}</pre>
          </div>
        </div>
      </div>
      
      <div class="card">
        <div class="card-header bg-light">
          <h3 class="h5 mb-0">   </h3>
        </div>
        <div class="card-body">
          <div class="chat-container mb-4">
            <div v-for="(message, index) in chatHistory" :key="index" 
                 :class="['chat-message', message.sender === 'user' ? 'user-message' : 'bot-message']">
              <div class="message-content">
                <p v-if="message.sender === 'user'">
                  <strong>:</strong> {{ message.content }}
                </p>
                <div v-else>
                  <p><strong>:</strong></p>
                  <pre class="bot-response">{{ message.content }}</pre>
                </div>
              </div>
            </div>
          </div>
          
          <form @submit.prevent="sendMessage" class="chat-input">
            <div class="input-group">
              <textarea 
                class="form-control" 
                v-model="userInput" 
                placeholder="   . : '  65 .'" 
                rows="3"
                :disabled="modifying"
              ></textarea>
              <button 
                type="submit" 
                class="btn btn-primary" 
                :disabled="!userInput.trim() || modifying"
              >
                <span v-if="modifying" class="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'ScenarioModify',
  props: {
    id: {
      type: [String, Number],
      required: true
    }
  },
  data() {
    return {
      userInput: '',
      chatHistory: [],
      modifying: false
    }
  },
  computed: {
    ...mapGetters(['currentScenario', 'isLoading', 'error'])
  },
  methods: {
    formatKey(key) {
      const keyMap = {
        gender: '',
        age: '',
        medical_history: '',
        family_history: '',
        allergies: '',
        vital_signs: '',
        symptoms: ''
      }
      
      return keyMap[key] || key
    },
    formatValue(value) {
      if (Array.isArray(value)) {
        return value.join(', ')
      }
      return value
    },
    async sendMessage() {
      if (!this.userInput.trim() || this.modifying) return
      
      //   
      this.chatHistory.push({
        sender: 'user',
        content: this.userInput
      })
      
      //   
      const userInput = this.userInput
      this.userInput = ''
      
      //    
      this.modifying = true
      
      try {
        //   
        const response = await this.$store.dispatch('modifyScenario', {
          scenarioId: this.id,
          userInput: userInput
        })
        
        //   
        this.chatHistory.push({
          sender: 'bot',
          content: '  .'
        })
        
        //   
        await this.$store.dispatch('fetchScenario', this.id)
      } catch (error) {
        //   
        this.chatHistory.push({
          sender: 'bot',
          content: ` : ${error.message || '   '}`
        })
      } finally {
        //    
        this.modifying = false
      }
    }
  },
  async created() {
    //   
    if (!this.currentScenario || this.currentScenario.id !== parseInt(this.id)) {
      await this.$store.dispatch('fetchScenario', this.id)
    }
    
    //    
    this.chatHistory.push({
      sender: 'bot',
      content: '!   .    ?'
    })
  }
}
</script>

<style scoped>
.scenario-modify {
  max-width: 900px;
  margin: 0 auto;
}

.scenario-content {
  max-height: 300px;
  overflow-y: auto;
  background-color: #f8f9fa;
  border-radius: 5px;
  padding: 15px;
}

.scenario-text {
  white-space: pre-wrap;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 0.9rem;
  margin: 0;
}

.chat-container {
  max-height: 400px;
  overflow-y: auto;
  border: 1px solid #dee2e6;
  border-radius: 5px;
  padding: 15px;
  background-color: #f8f9fa;
}

.chat-message {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
}

.user-message {
  align-items: flex-end;
}

.bot-message {
  align-items: flex-start;
}

.message-content {
  max-width: 80%;
  padding: 10px 15px;
  border-radius: 10px;
}

.user-message .message-content {
  background-color: #007bff;
  color: white;
}

.bot-message .message-content {
  background-color: #e9ecef;
  color: #212529;
}

.bot-response {
  white-space: pre-wrap;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 0.9rem;
  margin: 0;
}

.chat-input {
  margin-top: 15px;
}
</style> 