import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import mitt from 'mitt'
import axios from 'axios'

//   
const emitter = mitt()

// API  URL 
axios.defaults.baseURL = process.env.VUE_APP_API_URL || 'http://localhost:8000'

//      (GPT API  )
async function createSampleScenarios() {
  try {
    //  API     
    const response = await axios.get('/api/scenarios/', {
      params: { is_sample: true },
      withCredentials: true
    });
    
    if (response.data.length === 0) {
      console.log('    (GPT API )');
      
      //   1:   (GPT API )
      const scenario1Data = {
        title: "   ",
        main_disease: " ",
        personal_info: {
          age: "65",
          gender: "",
          occupation: ""
        },
        additional_info: {
          past_medical_history: ", ",
          family_history: "  ",
          allergies: "",
          major_symptoms: " , , "
        },
        include_conversation: true,
        is_sample: true
      };
      
      try {
        // GPT API    
        const gptResponse1 = await axios.post('/api/scenarios/generate/', scenario1Data, {
          withCredentials: true,
          headers: {
            'Content-Type': 'application/json'
          }
        });
        
        console.log('    :', gptResponse1.data);
        
        //      
      } catch (scenario1Error) {
        console.error('    :', scenario1Error);
        //     
        saveSampleToLocalStorage("   ", " ");
      }
      
      //   2:   (GPT API )
      const scenario2Data = {
        title: "   ",
        main_disease: " ",
        personal_info: {
          age: "22",
          gender: "",
          occupation: ""
        },
        additional_info: {
          past_medical_history: "1 (10)",
          family_history: "",
          allergies: "",
          major_symptoms: ", , , , "
        },
        include_conversation: true,
        is_sample: true
      };
      
      try {
        // GPT API   
        const gptResponse2 = await axios.post('/api/scenarios/generate/', scenario2Data, {
          withCredentials: true,
          headers: {
            'Content-Type': 'application/json'
          }
        });
        
        console.log('    :', gptResponse2.data);
        
        //      
      } catch (scenario2Error) {
        console.error('    :', scenario2Error);
        //     
        saveSampleToLocalStorage("   ", " ");
      }
      
      console.log('   ');
    } else {
      console.log('   ');
    }
  } catch (error) {
    console.error('     :', error);
    
    //      ( )
    try {
      console.log('     ');
      
      //    
      const hasSamples = checkLocalSamplesExist();
      if (!hasSamples) {
        //     
        saveSampleToLocalStorage("   ", " ");
        saveSampleToLocalStorage("   ", " ");
        console.log('     ');
      } else {
        console.log('     ');
      }
    } catch (localError) {
      console.error('     :', localError);
    }
  }
}

//      
function checkLocalSamplesExist() {
  try {
    const localScenarios = localStorage.getItem('tempScenarios');
    if (localScenarios) {
      const tempScenarios = JSON.parse(localScenarios);
      return tempScenarios.some(s => s.is_sample === true);
    }
  } catch (e) {
    console.error('    :', e);
  }
  return false;
}

//      
function saveSampleToLocalStorage(title, mainDisease) {
  try {
    //    
    let tempScenarios = [];
    const localScenarios = localStorage.getItem('tempScenarios');
    if (localScenarios) {
      tempScenarios = JSON.parse(localScenarios);
    }
    
    //   
    const sampleScenario = {
      id: Date.now().toString(),
      title: title,
      main_disease: mainDisease,
      is_sample: true,
      created_at: new Date().toISOString(),
      updated_at: new Date().toISOString(),
      created_by: {
        id: "admin-user-id",
        username: "Admin",
        email: "admin@example.com",
        is_admin: true
      },
      content: `# ${title} \n\n(GPT API      )`,
      patient_conversation: `#  \n\n****: ,    .\n\n****: , .`
    };
    
    //   
    tempScenarios.push(sampleScenario);
    localStorage.setItem('tempScenarios', JSON.stringify(tempScenarios));
    
    //  
    store.commit('SET_SCENARIOS', tempScenarios);
    
    return sampleScenario;
  } catch (error) {
    console.error('     :', error);
    return null;
  }
}

// Vue    
const app = createApp(App);

//    - $emitter    
app.config.globalProperties.$emitter = emitter;

//     $mitt 
app.config.globalProperties.$mitt = emitter;

//   
app.use(store);
app.use(router);

// 
app.mount('#app');

//       
store.dispatch('checkAuth').then(() => {
  createSampleScenarios();
}); 