<template>
  <div class="home">
    <div class="hero-section text-center py-5">
      <h1 class="display-4 mb-4">   </h1>
      <p class="lead mb-5">
               .<br>
        GPT-3.5       .
      </p>
      <div class="d-flex justify-content-center gap-3">
        <router-link :to="{ name: 'ScenarioList' }" class="btn btn-primary btn-lg">
            
        </router-link>
        <router-link v-if="isAdmin" :to="{ name: 'ScenarioCreate' }" class="btn btn-outline-primary btn-lg">
            
        </router-link>
        <router-link v-else-if="!isLoggedIn" :to="{ name: 'Login' }" class="btn btn-outline-primary btn-lg">
          
        </router-link>
      </div>
    </div>
    
    <div class="features-section py-5">
      <div class="container">
        <h2 class="text-center mb-5"> </h2>
        <div class="row g-4">
          <div class="col-md-4">
            <div class="card h-100">
              <div class="card-body text-center">
                <div class="feature-icon mb-3">
                  <i class="bi bi-file-earmark-text display-4"></i>
                </div>
                <h3 class="card-title h5"> </h3>
                <p class="card-text">
                            .
                    ,  ,     GPT-3.5   .
                </p>
              </div>
            </div>
          </div>
          
          <div class="col-md-4">
            <div class="card h-100">
              <div class="card-body text-center">
                <div class="feature-icon mb-3">
                  <i class="bi bi-chat-dots display-4"></i>
                </div>
                <h3 class="card-title h5">  </h3>
                <p class="card-text">
                         .
                       GPT-3.5   .
                </p>
              </div>
            </div>
          </div>
          
          <div class="col-md-4">
            <div class="card h-100">
              <div class="card-body text-center">
                <div class="feature-icon mb-3">
                  <i class="bi bi-search display-4"></i>
                </div>
                <h3 class="card-title h5"> </h3>
                <p class="card-text">
                         .
                  ,          .
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <div class="recent-scenarios-section py-5 bg-light">
      <div class="container">
        <h2 class="text-center mb-5">  </h2>
        
        <div v-if="loading" class="text-center">
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden"> ...</span>
          </div>
        </div>
        
        <div v-else-if="recentScenarios.length === 0" class="text-center">
          <p>   .</p>
        </div>
        
        <div v-else class="row row-cols-1 row-cols-md-3 g-4">
          <div class="col" v-for="scenario in recentScenarios" :key="scenario.id">
            <div class="card h-100">
              <div class="card-header bg-primary text-white">
                <h3 class="h5 mb-0 text-truncate" :title="scenario.title">{{ scenario.title }}</h3>
              </div>
              <div class="card-body">
                <p class="card-text"><strong> :</strong> {{ scenario.main_disease }}</p>
                <p class="card-text"><strong>:</strong> {{ formatDate(scenario.created_at) }}</p>
              </div>
              <div class="card-footer bg-white border-top-0">
                <router-link 
                  :to="{ name: 'ScenarioDetail', params: { id: scenario.id } }" 
                  class="btn btn-outline-primary w-100"
                >
                   
                </router-link>
              </div>
            </div>
          </div>
        </div>
        
        <div class="text-center mt-4">
          <router-link :to="{ name: 'ScenarioList' }" class="btn btn-outline-primary">
              
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Home',
  computed: {
    ...mapGetters(['scenarios', 'isLoading', 'isLoggedIn', 'isAdmin']),
    recentScenarios() {
      if (!this.scenarios || !Array.isArray(this.scenarios)) {
        console.warn('scenarios  :', this.scenarios);
        return [];
      }
      return this.scenarios.slice(0, 3);
    }
  },
  methods: {
    formatDate(dateString) {
      if (!dateString) return '  '
      
      const date = new Date(dateString)
      return new Intl.DateTimeFormat('ko-KR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      }).format(date)
    }
  },
  created() {
    //   
    this.$store.dispatch('fetchScenarios')
  }
}
</script>

<style scoped>
.hero-section {
  background-color: #f8f9fa;
  padding: 5rem 1rem;
  margin-bottom: 2rem;
  border-radius: 10px;
}

.features-section {
  margin-bottom: 2rem;
}

.feature-icon {
  color: #007bff;
}

.card {
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.card:hover {
  transform: translateY(-5px);
}

/* Bootstrap Icons CDN */
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.0/font/bootstrap-icons.css");
</style> 