<template>
  <div class="scenario-create">
    <h1 class="mb-4"> </h1>
    
    <div v-if="error" class="alert alert-danger">
      {{ error }}
    </div>
    
    <form @submit.prevent="createScenario" class="card">
      <div class="card-body">
        <div class="mb-3">
          <label for="title" class="form-label"> *</label>
          <input type="text" class="form-control" id="title" v-model="form.title" required>
        </div>
        
        <div class="mb-3">
          <label for="main_disease" class="form-label">  *</label>
          <input type="text" class="form-control" id="main_disease" v-model="form.main_disease" required>
        </div>
        
        <div class="mb-4">
          <label class="form-label">  </label>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="gender" id="gender-male" value="" v-model="form.personal_info.gender">
                <label class="form-check-label" for="gender-male"></label>
              </div>
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="gender" id="gender-female" value="" v-model="form.personal_info.gender">
                <label class="form-check-label" for="gender-female"></label>
              </div>
            </div>
          </div>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="row">
                <div class="col-md-6">
                  <div class="form-check form-check-inline" v-for="(age, index) in ageOptions" :key="index">
                    <input class="form-check-input" type="radio" name="age" :id="'age-' + index" :value="age" v-model="ageType" @change="generateRandomAge">
                    <label class="form-check-label" :for="'age-' + index">{{ age }}</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" name="age" id="age-custom" value="custom" v-model="ageType">
                    <label class="form-check-label" for="age-custom"> </label>
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="input-group" v-if="ageType === 'custom'">
                    <input type="number" class="form-control" placeholder=" " v-model="customAge" min="1" max="120">
                    <span class="input-group-text"></span>
                  </div>
                  <div v-else-if="ageType && form.personal_info.age" class="mt-2">
                     : {{ form.personal_info.age }}
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="history-none" value="" v-model="form.personal_info.medical_history_none" @change="toggleNone('medical_history')">
                <label class="form-check-label" for="history-none"></label>
              </div>
              <div v-if="!form.personal_info.medical_history_none">
                <div class="form-check" v-for="(history, index) in medicalHistoryOptions" :key="index">
                  <input class="form-check-input" type="checkbox" :id="'history-' + index" :value="history" v-model="form.personal_info.medical_history">
                  <label class="form-check-label" :for="'history-' + index">{{ history }}</label>
                </div>
                <div class="mt-2" v-if="form.personal_info.medical_history.includes('')">
                  <input type="text" class="form-control" placeholder="  " v-model="form.personal_info.medical_history_other">
                </div>
              </div>
            </div>
          </div>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="family-none" value="" v-model="form.personal_info.family_history_none" @change="toggleNone('family_history')">
                <label class="form-check-label" for="family-none"></label>
              </div>
              <div v-if="!form.personal_info.family_history_none">
                <div class="form-check" v-for="(history, index) in familyHistoryOptions" :key="index">
                  <input class="form-check-input" type="checkbox" :id="'family-' + index" :value="history" v-model="form.personal_info.family_history">
                  <label class="form-check-label" :for="'family-' + index">{{ history }}</label>
                </div>
                <div class="mt-2" v-if="form.personal_info.family_history.includes('')">
                  <input type="text" class="form-control" placeholder="  " v-model="form.personal_info.family_history_other">
                </div>
              </div>
            </div>
          </div>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="allergy-none" value="" v-model="form.personal_info.allergies_none" @change="toggleNone('allergies')">
                <label class="form-check-label" for="allergy-none"></label>
              </div>
              <div v-if="!form.personal_info.allergies_none">
                <div class="form-check" v-for="(allergy, index) in allergyOptions" :key="index">
                  <input class="form-check-input" type="checkbox" :id="'allergy-' + index" :value="allergy" v-model="form.personal_info.allergies">
                  <label class="form-check-label" :for="'allergy-' + index">{{ allergy }}</label>
                </div>
                <div class="mt-2" v-if="form.personal_info.allergies.includes('')">
                  <input type="text" class="form-control" placeholder="  " v-model="form.personal_info.allergies_other">
                </div>
              </div>
            </div>
          </div>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="vital_signs" id="vital-normal" value="" v-model="form.personal_info.vital_signs">
                <label class="form-check-label" for="vital-normal"></label>
              </div>
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="vital_signs" id="vital-abnormal" value="" v-model="form.personal_info.vital_signs">
                <label class="form-check-label" for="vital-abnormal"></label>
              </div>
              <div class="mt-2" v-if="form.personal_info.vital_signs === ''">
                <input type="text" class="form-control" placeholder="  " v-model="form.personal_info.vital_signs_detail">
              </div>
            </div>
          </div>
          
          <div class="card mb-3">
            <div class="card-body">
              <h5 class="card-title"></h5>
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="symptom-none" value="" v-model="form.personal_info.symptoms_none" @change="toggleNone('symptoms')">
                <label class="form-check-label" for="symptom-none"></label>
              </div>
              <div v-if="!form.personal_info.symptoms_none">
                <div class="form-check" v-for="(symptom, index) in symptomOptions" :key="index">
                  <input class="form-check-input" type="checkbox" :id="'symptom-' + index" :value="symptom" v-model="form.personal_info.symptoms">
                  <label class="form-check-label" :for="'symptom-' + index">{{ symptom }}</label>
                </div>
                <div class="mt-2" v-if="form.personal_info.symptoms.includes('')">
                  <input type="text" class="form-control" placeholder="  " v-model="form.personal_info.symptoms_other">
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <div class="mb-3">
          <label for="additional_info" class="form-label"> </label>
          <textarea class="form-control" id="additional_info" rows="5" v-model="form.additional_info" placeholder=" ,     "></textarea>
        </div>
        
        <div class="d-grid">
          <button type="submit" class="btn btn-primary" :disabled="loading">
            <span v-if="loading" class="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
             
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { getCurrentInstance } from 'vue'

export default {
  name: 'ScenarioCreate',
  data() {
    return {
      form: {
        title: '',
        main_disease: '',
        personal_info: {
          gender: '',
          age: '',
          medical_history: [],
          medical_history_other: '',
          medical_history_none: false,
          family_history: [],
          family_history_other: '',
          family_history_none: false,
          allergies: [],
          allergies_other: '',
          allergies_none: false,
          vital_signs: '',
          vital_signs_detail: '',
          symptoms: [],
          symptoms_other: '',
          symptoms_none: false
        },
        additional_info: ''
      },
      ageType: '',
      customAge: '',
      ageOptions: ['', '', '', '', ''],
      medicalHistoryOptions: ['', '', '', ''],
      familyHistoryOptions: ['', '', '', ''],
      allergyOptions: ['', '', ''],
      symptomOptions: ['', '', '', '']
    }
  },
  computed: {
    ...mapGetters(['isLoading', 'error']),
    loading() {
      return this.isLoading
    }
  },
  setup() {
    const { proxy } = getCurrentInstance()
    return { proxy }
  },
  watch: {
    customAge(newVal) {
      if (this.ageType === 'custom' && newVal) {
        this.form.personal_info.age = newVal + ''
      }
    }
  },
  methods: {
    //     
    generateRandomAge() {
      if (this.ageType === 'custom') return;
      
      let min, max;
      switch(this.ageType) {
        case '':
          min = 0; max = 1;
          break;
        case '':
          min = 2; max = 12;
          break;
        case '':
          min = 13; max = 19;
          break;
        case '':
          min = 20; max = 64;
          break;
        case '':
          min = 65; max = 90;
          break;
        default:
          return;
      }
      
      //     
      const randomAge = Math.floor(Math.random() * (max - min + 1)) + min;
      this.form.personal_info.age = randomAge + '';
    },
    toggleNone(field) {
      if (field === 'medical_history') {
        if (this.form.personal_info.medical_history_none) {
          this.form.personal_info.medical_history = []
        }
      } else if (field === 'family_history') {
        if (this.form.personal_info.family_history_none) {
          this.form.personal_info.family_history = []
        }
      } else if (field === 'allergies') {
        if (this.form.personal_info.allergies_none) {
          this.form.personal_info.allergies = []
        }
      } else if (field === 'symptoms') {
        if (this.form.personal_info.symptoms_none) {
          this.form.personal_info.symptoms = []
        }
      }
    },
    async createScenario() {
      try {
        //   
        this.proxy.$mitt.emit('set-loading', [true, '  .    1    .']);
        
        //   
        const personalInfo = { ...this.form.personal_info }
        
        //  
        if (this.ageType === 'custom' && this.customAge) {
          personalInfo.age = this.customAge + ''
        }
        
        //  
        if (personalInfo.medical_history_none) {
          personalInfo.medical_history = ['']
        } else if (personalInfo.medical_history.includes('') && personalInfo.medical_history_other) {
          const index = personalInfo.medical_history.indexOf('')
          personalInfo.medical_history[index] = ': ' + personalInfo.medical_history_other
        }
        
        //  
        if (personalInfo.family_history_none) {
          personalInfo.family_history = ['']
        } else if (personalInfo.family_history.includes('') && personalInfo.family_history_other) {
          const index = personalInfo.family_history.indexOf('')
          personalInfo.family_history[index] = ': ' + personalInfo.family_history_other
        }
        
        //  
        if (personalInfo.allergies_none) {
          personalInfo.allergies = ['']
        } else if (personalInfo.allergies.includes('') && personalInfo.allergies_other) {
          const index = personalInfo.allergies.indexOf('')
          personalInfo.allergies[index] = ': ' + personalInfo.allergies_other
        }
        
        //  
        if (personalInfo.symptoms_none) {
          personalInfo.symptoms = ['']
        } else if (personalInfo.symptoms.includes('') && personalInfo.symptoms_other) {
          const index = personalInfo.symptoms.indexOf('')
          personalInfo.symptoms[index] = ': ' + personalInfo.symptoms_other
        }
        
        //  
        if (personalInfo.vital_signs === '' && personalInfo.vital_signs_detail) {
          personalInfo.vital_signs = ': ' + personalInfo.vital_signs_detail
        }
        
        //   
        delete personalInfo.medical_history_other
        delete personalInfo.medical_history_none
        delete personalInfo.family_history_other
        delete personalInfo.family_history_none
        delete personalInfo.allergies_other
        delete personalInfo.allergies_none
        delete personalInfo.symptoms_other
        delete personalInfo.symptoms_none
        delete personalInfo.vital_signs_detail
        
        //   
        const scenarioData = {
          title: this.form.title,
          main_disease: this.form.main_disease,
          personal_info: personalInfo,
          additional_info: {
            past_medical_history: personalInfo.medical_history.join(', '),
            family_history: personalInfo.family_history.join(', '),
            allergies: personalInfo.allergies.join(', '),
            major_symptoms: personalInfo.symptoms.join(', '),
            description: this.form.additional_info
          }
        }
        
        //   
        const scenario = await this.$store.dispatch('createScenario', scenarioData)
        
        //   
        this.proxy.$mitt.emit('set-loading', [false, '']);
        
        //    
        this.$router.push({ name: 'ScenarioDetail', params: { id: scenario.id } })
      } catch (error) {
        //   
        this.proxy.$mitt.emit('set-loading', [false, '']);
        console.error('  :', error)
      }
    }
  }
}
</script>

<style scoped>
.scenario-create {
  max-width: 800px;
  margin: 0 auto;
}

.card {
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.card-title {
  font-size: 1.1rem;
  font-weight: 500;
  margin-bottom: 1rem;
}
</style> 