import { createStore } from 'vuex'
import axios from 'axios'
// Firebase  import 
// import { getAuth, signInWithEmailAndPassword, createUserWithEmailAndPassword, signOut, onAuthStateChanged } from 'firebase/auth'
// import { getFirestore, collection, addDoc, getDocs, doc, getDoc, updateDoc, query, orderBy, where } from 'firebase/firestore'

// API  URL 
axios.defaults.baseURL = process.env.VUE_APP_API_URL || 'http://localhost:8000'

// axios  
axios.interceptors.request.use(
  config => {
    // CSRF   (Django  )
    const csrfToken = document.cookie.match(/csrftoken=([^;]+)/);
    if (csrfToken) {
      config.headers['X-CSRFToken'] = csrfToken[1];
      console.log('CSRF  :', csrfToken[1]);
    }
    
    //  API     
    if (config.url.includes('/modifications/modify/')) {
      console.log('  API  :   ')
      //  API    
      config.headers['X-Special-Auth'] = 'true'
    }
    
    //     (!)
    config.withCredentials = true;
    
    //  
    console.log('API  URL:', config.url)
    console.log('API  :', config.headers)
    console.log('API  :', config.data)
    
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

//   
axios.interceptors.response.use(
  response => {
    //  
    console.log('API  :', response.status)
    console.log('API  :', response.data)
    return response
  },
  error => {
    //  
    console.error('API  :', error.response ? error.response.status : error.message)
    console.error('API  :', error.response ? error.response.data : error)
    return Promise.reject(error)
  }
)

const store = createStore({
  state: {
    user: null,
    scenarios: [],
    currentScenario: null,
    loading: false,
    error: null
  },
  getters: {
    isLoggedIn: state => !!state.user,
    currentUser: state => state.user,
    isAdmin: state => state.user && state.user.is_admin,
    scenarios: state => state.scenarios,
    currentScenario: state => state.currentScenario,
    isLoading: state => state.loading,
    error: state => state.error
  },
  mutations: {
    SET_USER(state, user) {
      state.user = user
    },
    SET_SCENARIOS(state, scenarios) {
      state.scenarios = scenarios
    },
    SET_CURRENT_SCENARIO(state, scenario) {
      state.currentScenario = scenario
    },
    SET_LOADING(state, loading) {
      state.loading = loading
    },
    SET_ERROR(state, error) {
      state.error = error
    },
    CLEAR_ERROR(state) {
      state.error = null
    }
  },
  actions: {
    //   
    checkAuth({ commit }) {
      //      
      const adminUser = localStorage.getItem('adminUser')
      if (adminUser) {
        commit('SET_USER', JSON.parse(adminUser))
        return
      }
      
      //       
      const tempUser = localStorage.getItem('tempUser')
      if (tempUser) {
        commit('SET_USER', JSON.parse(tempUser))
        return
      }
      
      // Firebase   
    },
    
    // 
    async login({ commit }, { email, password }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        //     (admin@example.com / password123!)
        if (email === 'admin@example.com' && password === 'password123!') {
          const adminUser = {
            id: 'admin-user-id',
            username: 'Admin',
            email: 'admin@example.com',
            is_admin: true
          }
          commit('SET_USER', adminUser)
          //      
          localStorage.setItem('adminUser', JSON.stringify(adminUser))
          commit('SET_LOADING', false)
          return adminUser
        }
        
        //    -  API 
        try {
          //   API 
          const response = await axios.post('/api/auth/login/', { email, password });
          
          if (response.data && response.data.user) {
            const user = response.data.user;
            commit('SET_USER', user);
            localStorage.setItem('tempUser', JSON.stringify(user));
            commit('SET_LOADING', false);
            return user;
          } else {
            throw new Error('    .');
          }
        } catch (apiError) {
          console.error('  API  :', apiError);
          
          //  API     (  )
          if (email && email.includes('@') && password && password.length >= 6) {
            const tempUser = {
              id: 'user-' + Date.now(),
              username: email.split('@')[0],
              email: email,
              is_admin: false,
              created_at: new Date().toISOString()
            }
            
            commit('SET_USER', tempUser)
            localStorage.setItem('tempUser', JSON.stringify(tempUser))
            commit('SET_LOADING', false)
            return tempUser
          } else {
            throw new Error('  6   .')
          }
        }
        
        // Firebase   
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 
    async register({ commit }, { username, email, password }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        //   API 
        try {
          const response = await axios.post('/api/auth/register/', {
            username,
            email,
            password
          });
          
          if (response.data && response.data.user) {
            const user = response.data.user;
            commit('SET_USER', user);
            localStorage.setItem('tempUser', JSON.stringify(user));
            commit('SET_LOADING', false);
            return user;
          } else {
            throw new Error('    .');
          }
        } catch (apiError) {
          console.error('  API  :', apiError);
          
          //  API     (  )
          const tempUser = {
            id: 'user-' + Date.now(),
            username,
            email,
            is_admin: false,
            created_at: new Date().toISOString()
          }
          
          commit('SET_USER', tempUser)
          localStorage.setItem('tempUser', JSON.stringify(tempUser))
          commit('SET_LOADING', false)
          return tempUser
        }
        
        // Firebase   
      } catch (error) {
        commit('SET_ERROR', error.message)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    // 
    async logout({ commit }) {
      try {
        //     
        localStorage.removeItem('adminUser')
        localStorage.removeItem('tempUser')
        
        //   API  ( )
        try {
          await axios.post('/api/auth/logout/');
        } catch (apiError) {
          console.error('  API  :', apiError);
          //  
        }
        
        commit('SET_USER', null)
        
        // Firebase   
        return true
      } catch (error) {
        console.error('  :', error)
        return false
      }
    },
    
    //   
    async fetchScenarios({ commit }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        console.log('   ...');
        
        //   
        const response = await axios.get('/api/scenarios/', {
          withCredentials: true
        })
        
        console.log('  :', response.data);
        
        //    
        let localScenarios = [];
        try {
          const localData = localStorage.getItem('tempScenarios');
          if (localData) {
            localScenarios = JSON.parse(localData);
            console.log('   :', localScenarios);
          }
        } catch (localError) {
          console.error('   :', localError);
        }
        
        //     
        // (ID     )
        const scenarioMap = {};
        
        //   
        response.data.forEach(scenario => {
          scenarioMap[scenario.id] = scenario;
        });
        
        //   
        localScenarios.forEach(scenario => {
          //   ID    
          if (!scenarioMap[scenario.id]) {
            scenarioMap[scenario.id] = {
              ...scenario,
              is_local: true //   
            };
          }
        });
        
        //    
        const mergedScenarios = Object.values(scenarioMap);
        console.log('  :', mergedScenarios);
        
        //     
        mergedScenarios.sort((a, b) => {
          return new Date(b.created_at) - new Date(a.created_at);
        });
        
        commit('SET_SCENARIOS', mergedScenarios)
        commit('SET_LOADING', false)
        return mergedScenarios
      } catch (error) {
        console.error('   :', error)
        
        // API      
        let localScenarios = [];
        try {
          const localData = localStorage.getItem('tempScenarios');
          if (localData) {
            localScenarios = JSON.parse(localData);
            localScenarios = localScenarios.map(scenario => ({
              ...scenario,
              is_local: true
            }));
            
            //     
            localScenarios.sort((a, b) => {
              return new Date(b.created_at) - new Date(a.created_at);
            });
            
            console.log(' ,    :', localScenarios);
            commit('SET_SCENARIOS', localScenarios);
            commit('SET_LOADING', false);
            return localScenarios;
          }
        } catch (localError) {
          console.error('   :', localError);
        }
        
        //    
        commit('SET_ERROR', '   .')
        commit('SET_LOADING', false)
        return []
      }
    },
    
    //   
    async fetchScenario({ commit, dispatch, state }, id) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        console.log(' ID  :', id);
        
        // ID   (  )
        if (id.toString().length >= 13) {
          console.log(' ID  -   ...');
          //     ID   
          if (state.currentScenario && state.currentScenario.id.toString() === id.toString()) {
            console.log('  :', state.currentScenario);
            commit('SET_LOADING', false);
            return state.currentScenario;
          }
          
          //   
          const foundScenario = state.scenarios.find(s => s.id.toString() === id.toString());
          if (foundScenario) {
            console.log('  :', foundScenario);
            commit('SET_CURRENT_SCENARIO', foundScenario);
            commit('SET_LOADING', false);
            return foundScenario;
          }
          
          //  
          try {
            const localScenarios = localStorage.getItem('tempScenarios');
            if (localScenarios) {
              const parsedScenarios = JSON.parse(localScenarios);
              const foundLocalScenario = parsedScenarios.find(s => s.id.toString() === id.toString());
              if (foundLocalScenario) {
                console.log(' :', foundLocalScenario);
                commit('SET_CURRENT_SCENARIO', foundLocalScenario);
                commit('SET_LOADING', false);
                return foundLocalScenario;
              }
            }
          } catch (localError) {
            console.error('   :', localError);
          }
          
          throw new Error('   ID.');
        }
        
        //  API  
        console.log(' API  :', id);
        const response = await axios.get(`/api/scenarios/${id}/`, {
          withCredentials: true
        });
        
        console.log(' API :', response.data);
        const scenario = response.data;
        
        //  
        commit('SET_CURRENT_SCENARIO', scenario);
        commit('SET_LOADING', false);
        
        //       
        if (!scenario.patient_conversation || scenario.patient_conversation.trim() === '') {
          await dispatch('setDefaultPatientConversation', scenario);
        }
        
        return scenario;
      } catch (error) {
        console.error('  :', error)
        commit('SET_ERROR', '    .')
        commit('SET_LOADING', false)
        
        //     (UI    )
        return {
          id: id,
          title: '   ',
          main_disease: '',
          personal_info: {},
          additional_info: {},
          content: '   .  .',
          patient_conversation: '',
          created_at: new Date().toISOString(),
          created_by: null
        }
      }
    },
    
    //  
    async createScenario({ commit, getters, state }, scenarioData) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        //   
        const currentUser = getters.currentUser
        if (!currentUser) {
          throw new Error('   .')
        }
        
        console.log('GPT API    ...');
        console.log(' :', scenarioData);
        
        try {
          //  API  GPT   
          const gptResponse = await axios.post('/api/scenarios/generate/', {
            title: scenarioData.title,
            main_disease: scenarioData.main_disease,
            personal_info: scenarioData.personal_info || {},
            additional_info: scenarioData.additional_info || {},
            include_conversation: true //    
          }, {
            withCredentials: true,
            headers: {
              'Content-Type': 'application/json'
            }
          });
          
          console.log('GPT  :', gptResponse.data);
          
          if (!gptResponse.data) {
            throw new Error('GPT API  .');
          }
          
          const savedScenario = gptResponse.data;
          
          //   
          commit('SET_CURRENT_SCENARIO', savedScenario);
          
          // scenarios   (  )
          const updatedScenarios = [...state.scenarios, savedScenario];
          commit('SET_SCENARIOS', updatedScenarios);
          
          commit('SET_LOADING', false);
          return savedScenario;
        } catch (apiError) {
          console.error(' DB  ,   :', apiError);
          
          // GPT API       
          console.log('GPT API  ,   ...');
          
          //  ID 
          const tempId = Date.now().toString();
          console.log(' ID :', tempId);
          
          //   
          const defaultContent = `
# ${scenarioData.title} 

##  
- :  ()
- : ${scenarioData.personal_info?.age || '40'}
- : ${scenarioData.personal_info?.gender || ''}
- : ${scenarioData.personal_info?.occupation || ''}
- : ${scenarioData.additional_info?.past_medical_history || ''}
- : ${scenarioData.additional_info?.family_history || ''}
- : ${scenarioData.additional_info?.allergies || ''}

##  
- ${scenarioData.main_disease}
- ${scenarioData.additional_info?.major_symptoms || ', , '}

##  
- : 
- : BP 130/80 mmHg, HR 85 bpm, RR 18/min, SpO2 97%, BT 37.0C

##  
1.  
2.  
3.  
4.  
5.    

##  
-   
-  
-     
`;

          const defaultConversation = `
#  

##  
****: ,    .  ?  ?

****: , . ${scenarioData.additional_info?.major_symptoms || '   .'} 

****:    ?

****:    .    .

##    
****:   . ${scenarioData.main_disease} .         .

****: .   ?

****:       .      .

## 
****:     .     .

****: ,    .    ?

****:      .       .
`;

          //   
          const tempScenario = {
            id: tempId,
            title: scenarioData.title,
            main_disease: scenarioData.main_disease,
            personal_info: scenarioData.personal_info || {},
            additional_info: scenarioData.additional_info || {},
            content: defaultContent,
            patient_conversation: defaultConversation,
            created_at: new Date().toISOString(),
            updated_at: new Date().toISOString(),
            created_by: getters.currentUser,
            is_local: true //    
          };
          
          //   
          try {
            //    
            let tempScenarios = [];
            const localScenarios = localStorage.getItem('tempScenarios');
            if (localScenarios) {
              tempScenarios = JSON.parse(localScenarios);
            }
            
            //   
            tempScenarios.push(tempScenario);
            
            //   
            localStorage.setItem('tempScenarios', JSON.stringify(tempScenarios));
            console.log('    ');
            
            //  
            const updatedScenarios = [...state.scenarios, tempScenario];
            commit('SET_SCENARIOS', updatedScenarios);
            commit('SET_CURRENT_SCENARIO', tempScenario);
          } catch (storageError) {
            console.error('   :', storageError);
          }
          
          commit('SET_LOADING', false);
          return tempScenario;
        }
      } catch (error) {
        console.error('  :', error);
        commit('SET_ERROR', `    : ${error.message}`)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    //  
    async modifyScenario({ commit, getters, state }, { scenarioId, userInput }) {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      try {
        //   
        const currentUser = getters.currentUser
        if (!currentUser) {
          throw new Error('   .')
        }
        
        //  ID  ( )
        const isLocalId = scenarioId.toString().length >= 13;
        console.log(` ID : ${scenarioId},  ID : ${isLocalId}`);
        
        if (isLocalId) {
          console.log(' ID  -    ...');
          //    
          try {
            const localScenarios = localStorage.getItem('tempScenarios');
            if (localScenarios) {
              const tempScenarios = JSON.parse(localScenarios);
              const scenarioIndex = tempScenarios.findIndex(s => s.id.toString() === scenarioId.toString());
              
              if (scenarioIndex !== -1) {
                const existingScenario = tempScenarios[scenarioIndex];
                console.log('  :', existingScenario);
                
                try {
                  console.log('GPT   ...');
                  
                  //     (GPT API   )
                  const updatedContent = this._handleLocalModification(existingScenario.content, userInput);
                  const updatedConversation = this._handleLocalModification(existingScenario.patient_conversation, userInput);
                  
                  //   
                  const modifiedScenario = {
                    ...existingScenario,
                    content: updatedContent,
                    patient_conversation: updatedConversation,
                    updated_at: new Date().toISOString()
                  };
                  
                  //   
                  tempScenarios[scenarioIndex] = modifiedScenario;
                  localStorage.setItem('tempScenarios', JSON.stringify(tempScenarios));
                  console.log('    ');
                  
                  //  
                  commit('SET_CURRENT_SCENARIO', modifiedScenario);
                  commit('SET_SCENARIOS', tempScenarios);
                  
                  commit('SET_LOADING', false);
                  return modifiedScenario;
                } catch (gptError) {
                  console.error('  :', gptError);
                  throw new Error(`  : ${gptError.message}`);
                }
              } else {
                throw new Error(`ID ${scenarioId}     .`);
              }
            } else {
              throw new Error('   .');
            }
          } catch (localError) {
            console.error('   :', localError);
            throw new Error(`   : ${localError.message}`);
          }
        }
        
        //  API   
        console.log(' API    ...');
        try {
          const response = await axios.post(`/api/modifications/modify/${scenarioId}/`, {
            user_input: userInput,
            created_by: currentUser.id
          }, {
            withCredentials: true,
            headers: {
              'Content-Type': 'application/json'
            }
          });
          
          console.log('   :', response.data);
          
          //   
          const modifiedScenario = response.data;
          
          //   
          commit('SET_CURRENT_SCENARIO', modifiedScenario);
          commit('SET_LOADING', false);
          return modifiedScenario;
        } catch (apiError) {
          console.error('   API  :', apiError);
          
          //  API    
          if (apiError.response) {
            throw new Error(`  (${apiError.response.status}): ${apiError.response.data.error || '   '}`);
          } else {
            throw new Error('   .   .');
          }
        }
      } catch (error) {
        console.error('  :', error)
        commit('SET_ERROR', `    : ${error.message}`)
        commit('SET_LOADING', false)
        throw error
      }
    },
    
    //     
    _handleLocalModification(content, userInput) {
      if (!content) return '';
      
      //    ( GPT API   )
      let modifiedContent = content;
      
      //     
      if (userInput.includes('') || userInput.includes('')) {
        //  
        const ageMatch = userInput.match(/(\d+)/);
        if (ageMatch && ageMatch[1]) {
          modifiedContent = modifiedContent.replace(/(\d+)/, `${ageMatch[1]}`);
        }
      }
      
      if (userInput.includes('') || userInput.includes('') || userInput.includes('')) {
        //  
        if (userInput.includes('') || userInput.includes('')) {
          modifiedContent = modifiedContent.replace(/: (|)/g, ': ');
        } else if (userInput.includes('') || userInput.includes('')) {
          modifiedContent = modifiedContent.replace(/: (|)/g, ': ');
        }
      }
      
      //   
      modifiedContent += `\n\n##  \n- ${new Date().toLocaleString()}: "${userInput}"   `;
      
      return modifiedContent;
    },
    
    //    
    async setDefaultPatientConversation({ commit }, scenario) {
      console.log('    ...')
      
      const defaultConversation = `
#  

##  
****: ,    .  ?  ?

****: , .          .        .

****:    ?     ?

****:    .    , 3        .         .

****:   ?    , , ,    .

****:    .       .      .    .

##    
****:   .   ,  X-ray   ,    .         .

****: ?    ?  .   ?

****:      ,     .          .  ,        .        .

****: .   .   ?  ?

****: ,        .      ,       .   ,     .

****: .     ,  .        ?

****:         .             .  , , ,    .    ,       .

## 
****: 3     .  ,   .    .

****: ,    .     ,        .      ?

****:       .       .       (2-3  1.5kg )  .         .

****: .    ?    ?

****:    ,    2g    . , ,  ,     .     1.5-2   .       .

****:     ?    .

****:    .  10-15      .        .           . 2     ,       .

****:  .   ,        .      .

****: ,  .   .       .          .         .
      `
      
      //     
      scenario.patient_conversation = defaultConversation
      
      //    
      commit('SET_CURRENT_SCENARIO', { ...scenario })
      
      //  API   
      try {
        await axios.patch(`/api/scenarios/${scenario.id}/`, {
          patient_conversation: defaultConversation
        });
        console.log('   ')
      } catch (error) {
        console.warn('   :', error)
      }
      
      return scenario
    }
  }
})

export default store
